.product_container {
  display: flex;
  padding: 5px;
}

.product_item {
  width: 23%;
  margin: 3px 6px;
}

.product_image {
  width: 100%;
  min-height: 100px;
  max-height: 180px;
  padding: 2px;
  border: 1px solid #000;
}

.productPriceDesc {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 20px 0;
}

.product_btn {
  display: block;
  width: 100%;
  border: 0;
  background-color: #2a5ea5;
  color: #fff;
  padding: 7px;
  border-radius: 6px;
  cursor: pointer;
}

.product_info img {
  width: 40%;
  display: block;
  margin: 20px auto;
}
.product_info p {
  color: #2a5ea5;
  font-size: 20px;
  margin: 10px 0;
  text-align: center;
}

.close_icon {
  position: absolute;
  right: 10px;
  top: 10px;
  cursor: pointer;
  font-size: 20px;
}