*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

#root{
  height: 100vh;
}

.container{
  display: grid;
  grid-template-areas: 
  "header"
  "main"
  "footer";
  height: 100%;
  grid-template-rows: 50px 1fr 50px;
  
}


main{
  grid-area: main;


}

.mainContainer{
  display: flex;
  justify-content: space-around;
}

.productsContainer{
  width: 68%;
  flex-wrap: wrap;
}

.filterContainer{
  width: 30%;
}




